.modal {
  border: none;
  border-radius: 8px;
  padding: 2rem;
  background-color: #d7fcf8;
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 50;
  transform: translate(-50%, -50%);
  max-width: 90%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.modal[open] {
  animation: slide-in-from-top 0.35s ease-out;
}

.modal::backdrop {
  background: rgba(0, 0, 0, 0.9);
}

.modal h2 {
  font-family: "Handjet", monospace;
  margin: 0 0 1.25rem 0;
  font-size: 2.5rem;
  text-transform: uppercase;
  text-align: center;
}

.modal p {
  margin: 0.5rem 0;
  font-size: 1.2rem;
  text-align: center;
}

.buttonGroup {
  display: flex;
  justify-content: space-around;
  margin-top: 1.5rem;
}

.yesButton,
.noButton {
  padding: 0.5rem 1.5rem;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
}

.yesButton {
  background-color: #28a745;
  color: white;
}

.yesButton:hover {
  background-color: #218838;
}

.noButton {
  background-color: #dc3545;
  color: white;
}

.noButton:hover {
  background-color: #c82333;
}
